.html-editor {
  height: 300px;
  background-color: #282828;
  color: #EBDBB2;
  padding: 10px;
  white-space: pre-wrap;
  font-size: 16px;
  display: block;
  overflow: auto;
  -webkit-transition: all 0.5s linear;
  -o-transition: all 0.5s linear;
  transition: all 0.5s linear;
}
.html-editor:focus {
  outline: none;
  -webkit-box-shadow: 0px 0px 10px 0px #1D2021;
          box-shadow: 0px 0px 10px 0px #1D2021;
}



